<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col s12 m8 offset-m2">
          <div class="login card-panel white darken-1 black-text center">
            <h3>회원등록</h3>
            <form>
              <div class="input-field">
                <i class="material-icons prefix">email</i>
                <input type="text" id="email" v-model="email">
                <label for="email">Email</label>
              </div>
              <div class="input-field">
                <i class="material-icons prefix">lock</i>
                <input type="password" id="password" v-model="password">
                <label for="password">Password</label>
              </div>
              <button v-on:click="register" class="btn btn-large grey lighten-4 black-text">등록</button>

            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'register',
  data: function() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    register: function(e) {
      firebase
      .auth()
      .createUserWithEmailAndPassword(this.email, this.password)
      .then(
        user => {
        alert('회원이 되신것을 축하드립니다');
        this.$router.push('/');
      },
      err => {
        alert(err.message);
      }
    );
      e.preventDefault();
    }
  }
}
</script>

<style>
</style>



